.color-grid-container {
  @include flexTop;
  flex-direction: column;

  .color-grid {
    @include flex(flex-start, flex-start);
    flex-wrap: wrap;
    box-sizing: border-box;
    background: #444;
    width: 100%;
  }
}

.compare-color-options {
  .color-inputs {
    @include flex;

    input[type="color"] {
      padding: 0;
      margin: 0;
      background: transparent;
    }

    input[type="text"] {
      width: 50px;
      text-align: center;
    }
  }
}

.sort-info-options {
  button {
    width: 100px;
    margin: 0;
    border: none;
  }

  .off {
    @extend %label;
  }

  .on {
    @extend %checked;
  }
}

.distance-options {
  input[type="radio"] {
    display: none;
  }

  input[type="radio"] + label {
    @extend %label;
  }

  input[type="radio"]:checked + label {
    @extend %checked;
  }
}
